<template>
  <div>
    <div class="header">
      <img class="logo" src="../assets/logo.jpg" alt="" />
      <ul class="header_ul">
        <li><a href="/index" style="color:black"> 首页</a></li>
        <li>散文</li>
        <li>诗歌</li>
        <li>推荐</li>
        <li>原创</li>
      </ul>
    </div>
    <div class="login2">
      <!-- <img :src="imgSrc" width="100%" height="100%" alt="" /> -->
      <div class="box2">
        <h3 class="h3">欢迎注册</h3>
        <el-form
          :model="registerData"
          :rules="rules"
          ref="registerFrom"
          lable-width="100px"
          class="demo-ruleForm"
        >
          <el-form-item label="账号" prop="username">
            <el-input v-model="registerData.username"></el-input>
          </el-form-item>
          <el-form-item label="密码" prop="password">
            <el-input
              type="password"
              v-model="registerData.password"
            ></el-input>
          </el-form-item>
          <el-form-item label="确认密码" prop="password2">
            <el-input
              type="password"
              v-model="registerData.password2"
            ></el-input>
          </el-form-item>
          <el-form-item label="用户名" prop="name">
            <el-input v-model="registerData.name"></el-input>
          </el-form-item>
          <p class="Register-p"><a href="/login"> 已有账号？立即登录</a></p>
          <div class="item-button2">
            <el-button type="primary" @click="submitForm('registerFrom')"
              >立刻注册</el-button
            >
          </div>
        </el-form>
      </div>
    </div>
  </div>
</template>
<style scoped>
@import "../css/register.css";
</style>
<script>
export default {
  data() {
    var validatePass = (rule, value, callback) => {
      if (value !== this.registerData.password) {
        callback(new Error("两次输入密码不一致"));
      } else {
        callback();
      }
    };
    return {
      registerData: {
        username: "",
        password: "",
        password2: "",
        name: "",
      },
      rules: {
        username: [
          { required: true, message: "账号不得为空", trigger: "blur" },
          {
            min: 3,
            max: 32,
            message: "长度在3~32位之间",
            trigger: "blur",
          },
        ],
        password: [
          { required: true, message: "密码不得为空", trigger: "blur" },
          {
            min: 3,
            max: 32,
            message: "长度在3~32位之间",
            trigger: "blur",
          },
        ],
        password2: [
          { required: true, message: "确认密码不得为空", trigger: "blur" },
          {
            min: 3,
            max: 32,
            message: "长度在3~32位之间",
            trigger: "blur",
          },
          { validator: validatePass, trigger: "blur" },
        ],
        name: [
          { required: true, message: "用户名不得为空", trigger: "blur" },
          {
            min: 3,
            max: 18,
            message: "长度在3~18位之间",
            trigger: "blur",
          },
        ],
      },
    };
  },
  methods: {
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          // alert("注册成功");
          this.$axios.post("/register", this.registerData).then((res) => {
            // console.log(res.data);
            this.$message({
              type: "success",
              message: "注册成功",
            });
            this.$router.push("/login");
          });
        } else {
          alert("注册失败，请检查网络后重试");
          return false;
        }
      });
    },
  },
};
</script>
