<template>
  <div>
    <div class="header">
      <img class="logo" src="../assets/logo.jpg" alt="" />
      <ul class="header_ul">
        <li><a href="/index" style="color:black"> 首页</a></li>
        <li>散文</li>
        <li>诗歌</li>
        <li>推荐</li>
        <li>原创</li>
      </ul>
    </div>
    <div class="best">
      <div class="box">
        <h3 style="color:white">欢迎登录</h3>
        <el-form
          :model="registerData"
          :rules="rules"
          ref="registerFrom"
          lable-width="100px"
          class="demo-ruleForm"
        >
          <el-form-item label="账号" prop="name">
            <el-input v-model="registerData.name"></el-input>
          </el-form-item>
          <el-form-item label="密码" prop="password">
            <el-input
              type="password"
              class="input_inner"
              v-model="registerData.password"
            ></el-input>
          </el-form-item>
          <p class="login-p"><a href="/register"> 没有账号？立即注册</a></p>
          <div class="item-button">
            <el-button type="primary" @click="submitForm('registerFrom')"
              >登录</el-button
            >
          </div>
        </el-form>
      </div>
    </div>
  </div>
</template>
<style scoped>
@import "../css/login.css";
</style>
<script>
export default {
  data() {
    return {
      registerData: {
        name: "",
        password: "",
      },
      rules: {
        name: [
          { required: true, message: "账号不得为空", trigger: "blur" },
          {
            min: 3,
            max: 32,
            message: "长度在3~32位之间",
            trigger: "blur",
          },
        ],
        password: [
          { required: true, message: "密码不得为空", trigger: "blur" },
          {
            min: 3,
            max: 32,
            message: "长度在3~32位之间",
            trigger: "blur",
          },
        ],
      },
    };
  },
  methods: {
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          // alert("注册成功!");
          this.$axios.post("/login", this.registerData).then((res) => {
            // console.log(res.data.config.name);
            this.$message({
              type: "success",
              message: "登录成功",
            });
            // localStorage.setItem("elementToken", "Bearer" + res.data);
            this.$router.push("/index");
          });
        } else {
          alert("登录失败，请检查网速或者重新注册!!");
          return false;
        }
      });
    },
  },
};
</script>
