import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    uname: sessionStorage.getItem("name"),
  },
  mutations: {
    updateUser(state, args) {
      // srate: 固定的参数1，代表上方的state属性
      // args: 参数；将会设计成:{uid:xxx,uname:xxx,phone:xxx}
      state.uname = args.name;
      if (args.name) {
        sessionStorage.setItem("name", args.name);
      } else {
        sessionStorage.clear(); // 退出操作；清空
      }
    },
  },
  actions: {},
  modules: {},
});
